.song-container {
	min-height: 60vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	img {
		width: 20%;
		border-radius: 50%;
	}
	h2 {
		padding: 2rem 1rem 1rem 1rem;
		font-size: 1.9rem;
	}
	h3 {
		font-size: 1.3rem;
	}
}

@media screen and (max-width: 768px) {
	.song-container {
		img {
			width: 60%;
		}
	}
}
